<script>
  import { sourcecodeA, sourcecodeB, sourcecodeC, sourcecodeD, sourcecodeE } from './template'

  export default {
    data () {
      return {
        sourcecodeA,
        sourcecodeB,
        sourcecodeC,
        sourcecodeD,
        sourcecodeE,
        percentage: '70',
        status: 'info',
        striped: true
      }
    },
    methods: {
      increase () {
        if (this.percentage === 100) return
        this.percentage = parseInt(this.percentage, 10) + 10
      },
      decrease () {
        if (this.percentage === 0) return
        this.percentage = parseInt(this.percentage, 10) - 10
      }
    }
  }
</script>

