export const sourcecodeE = `<div class="vd-demo__block vd-demo__block-2">
<vm-progress type="circle" :percentage="70" :status="status"></vm-progress>
</div>
<div class="vd-demo__block vd-demo__block-2">
<vm-button-group>
  <vm-button type="default" @click="status = 'exception'"></vm-button>
  <vm-button type="default" @click="status = 'info'"></vm-button>
  <vm-button type="default" @click="status = 'warning'"></vm-button>
  <vm-button type="default" @click="status = 'success'"></vm-button>
</vm-button-group>
<vm-button-group>
  <vm-button type="default" @click="increase"></vm-button>
  <vm-button type="default" @click="decrease"></vm-button>
</vm-button-group>
</div>

<script>
  export default {
    data () {
      return {
        percentage: 70,
        status: 'info'
      }
    },
    methods: {
      increase () {
        if (this.percentage === 100) return
        this.percentage += 10
      },
      decrease () {
        if (this.percentage === 0) return
        this.percentage -= 10
      }
    }
  }
</script>
`


